.choices__button {
  background-image: url(data:image/png;base64,);
}

.swiper-arrow {
  border: 1px solid #3d5653;
  color: #3d5653;
}

.text-name {
  color: #6c7084;
}

.data-create-bg {
  background: linear-gradient(to top,#e7faf6,#e5f3fb);
}

.text-left {
  text-align: left;
}

// data-preprocess
.variable-list {
  a {
    text-align: left;
    display: flex;
    align-items: center;

    &.active {
      border: 1px solid #5380b2;
      h5 {
        color: #235185 !important;
      }
    }
  }
}

// feature-selection
.variable-table{
  th {
    padding: 2px 1px;
    
    label {
      // padding-left: 0 !important;
      text-align: center;
    }
  }
  td {
    &.active {
      background-color: #e5ecf4;
    }
  }
}

.custom-scrollbar{
  overflow: overlay;
  &::-webkit-scrollbar {
    width: 6px;
  }
  &::-webkit-scrollbar-track{
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 3px;
    background-color: rgb(61, 86, 83, .5);
  }
  &::-webkit-scrollbar-button {
    width: 0;
    height: 0;
  }
}

.dataSampling-card {
  .card {
      &.active {
      border-color: #3d5653 !important;
    }
  }
  .card {
    cursor: pointer;
    &.default {
      cursor: auto;
      background-color: #eff7f5;
    }
  }
}

.select-text-bg {
  background-color: #ddf2f3;
  border-color: #3d5653 !important;
  color: #3d5653;
}
.full-use {
  min-width: 85px;
  &.form-check-right {
    &.form-switch {
      .form-check-input {
        margin-right: 0;
      }
    }
    label {
      &.active {
        font-weight: 700;
      }
    }
  }
}
.accordion-radio {
  position: relative;
  input[type="radio"] {
    position: absolute;
    top: 16px;
    left: 20px;
    z-index: 4;

    &:checked {
      + label {
        .accordion-item {
          border-color: #3d5653;
        }
      }
    }
  }
  label {
    display: block;
  }
  .accordion-button {
    &:not(.collapsed) {
      background-color: #e5ecf4;
    }
  }
}

// data-sampling
.sampling-radio {
  position: relative;
  input[type="radio"] {
    position: absolute;
    top: 16px;
    right: 16px;
    margin-top: 0;
    z-index: 4;

    &:checked {
      .card {
        border-color: #3d5653;
      }
    }
  }
  label {
    display: block;
  }
}

.backslash {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg"><line x1="0" y1="0" x2="100%" y2="100%" stroke="rgb(233, 235, 236)" /></svg>');
}

// model-result
.model-result-list {
  .nav-item {
    + .nav-item {
      margin-top: 0.5rem;
    }
  }
  .nav-link {
    border: 1px solid #e5ecf4;
    border-radius: 0.25rem;
    &.active {
      border-color: #3d5653 !important;
      box-shadow: 0 1px 2px rgba(56,65,74,.15) !important;
    }
  }
}

// detetion-learning
.accordion-search {
  .accordion-button {
    padding: 16px 0;
    &.collapsed {
      box-shadow: none;
    }
    &:not(.collapsed) {
      background-color: transparent;
    }
    &::after {
      margin-left: 5px;
    }
  }
  .accordion-body {
    padding: 16px 0;
  }
}
.project-search-list {
  &.active {
    .card {
      border-color: #3d5653;
    }
  }
}